import { _decorator, Component, Node, Prefab, CCInteger, instantiate } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('PeopleController')
export class PeopleController extends Component {


    @property(Prefab)
    people: Prefab = null;

    @property(Node)
    peopleContainer: Node = null;

    @property(CCInteger)
    peopleTotal: number = 0;

    start() {
        this.loadPeople();
    }

    // update(deltaTime: number) {

    // }

    loadPeople() {
        for (let i = 0; i < this.peopleTotal; i++) {
            let peopleNode = instantiate(this.people);

            peopleNode.setPosition(-210 + Math.floor(i % 4) * 150, 570 - Math.floor(i / 4) * 240);

            this.peopleContainer.addChild(peopleNode);
        }
    }
}

